@keyframes fadeIn {
  from {
    opacity: 0;
    z-index: 10;
  }

  to {
    opacity: 1;
    z-index: 10;
    visibility: visible;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
    z-index: 10;
  }

  to {
    opacity: 0;
    visibility: hidden;
    z-index: -1;
  }
}

