.modal-dialog {
  max-width: 54vw;
  padding: 30px 80px 20px;
  background-color: #FFF;
}

.modal-dialog .modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: initial;
  border: none;
  border-radius: 0;
  outline: 0;
}

.modal-dialog .modal-content .modal-body {
  padding: 0;
}

.modal-dialog .modal-content .modal-body .close {
  display: inline-block;
  width: 20px;
  position: absolute;
  top: 10px;
  right: -50px;
  border: none;
  outline: none;
  opacity: 0.8;
}

.modal-dialog .modal-content .modal-body .carousel-wrap .owl-stage-outer {
  padding-right: 0;
}

.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav {
  width: 100%;
  position: absolute;
  top: 50%;
  margin-top: -15px;
}

.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav button {
  padding: 0;
  border: none;
  background: none;
}

.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav button img {
  width: 18px;
  height: 30px;
  opacity: 0.8;
}

.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav button.focus,
.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav button:focus,
.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav button.active,
.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav button:active {
  outline: none;
  box-shadow: none;
  background-color: transparent;
  border-color: transparent;
  background: none;
}

.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav button.owl-prev {
  float: left;
  margin-left: -45px;
}

.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav button.owl-next {
  float: right;
  margin-right: -45px;
}

.modal-dialog .modal-content .modal-body .carousel-wrap .owl-nav button.owl-next img {
  width: 18px;
  height: 30px;
  opacity: 0.8;
}

